<template>
   <div id="detail">
       <detailnav class="nav" @title='title' ref="nav"/>
       <div class="showCart" v-show="!showcart"></div>
       <scroll class="content" ref="scroll" :probe="3" @scroll="contentScroll">
          <detailshuf :topimages="topimages"/>
          <detailbase :goods="goods"/>
          <detailshop :shop="shop"/>
          <detailinfo :detailInfo="detailInfo" @imageLoad="imageLoad"/>
          <paraminfo ref="params" :paramInfo="paramInfo"/>
          <detailcomment ref="comment" :commentInfo="commentInfo"/>
          <detaillist ref="recommend" :good="recommend" @imageLoad="imageLoad"/>
       </scroll>
       <top @click.native="backTop" v-show="istop" />
       <detailbar @addtocart="addtocart"/>
   </div>
</template>
<script>
	import detailnav from "./detailnav"
	import {request} from "@/network/request"
	import detailshuf from './detailshuf'
	import {Goods, Shop, GoodsParam} from "@/network/detaill"
	import detailbase from "./detailBase"
	import detailshop from "./detailshop"
	import detailinfo from "./detailInfo"
	import paraminfo from "./paramInfo"
	import detailcomment from "./detailcomment"
	import scroll from '@/components/bscroll/scroll'
	import detaillist from './detaillist'
	import detailbar from './detailbar'
	import top from '@/components/top/top'
	export default{
		name:"detail",
		data(){
			return{
				iid:null,
				topimages:[],
				goods:{},
				shop:{},
				detailInfo:{},
				paramInfo:{},
				commentInfo:{},
				recommend:[],
				themTopY:[],
				currentIndex:0,
				istop:false,
				tabTop:0,
                istab:false,
                showcart:true
			}
		},
		created(){
			this.iid = this.$route.params.id;
			//
			this.detailid();
			//
			this.recommendlist();
		},
		methods:{
			addtocart(){
				//
				let time1=null;
				clearTimeout(time1)
				this.showcart = false;
                time1 = setTimeout(()=>{
                     this.showcart = true;
                },700)
				//
               const product = {}
               product.image = this.topimages[0];
               product.title = this.goods.title;
               product.desc = this.goods.desc;
               product.price = this.goods.realPrice;
               product.iid = this.iid;
               product.checked = false;
               this.$store.commit('addcart',product);
			},
			backTop(){
                //scroll
                this.$refs.scroll.scrollTo(0,0,500);
             },
			contentScroll(position){
               //top
               this.istop = position.y < -1000;
               this.istab = (-position.y) >= (this.tabTop-40);
              //,
              const positionY = -position.y;
              let length = this.themTopY.length;
              for(let i =0;i<length;i++){
                 if(this.currentIndex !== i && ((i<length -1 && positionY >= this.themTopY[i] && positionY < this.themTopY[i+1]) || (i === length -1 && positionY >= this.themTopY[i]))){
                    this.currentIndex = i;
                    //nav,
                    this.$refs.nav.currentIndex = this.currentIndex;
                 }
              }
			},
			title(index){
				//
                this.$refs.scroll.scrollTo(0,-this.themTopY[index],300);
			},
			recommendlist(){
              request({
                url:"/recommend"
              }).then(res =>{
                this.recommend = res.data.data.list;
              }).catch(err =>{
                console.log(''+err)
              })
			},
			imageLoad(){
               this.$refs.scroll.refresh();
               //
                   this.themTopY = [];
                   this.themTopY.push(0);
                   this.themTopY.push(this.$refs.params.$el.offsetTop);
                   this.themTopY.push(this.$refs.comment.$el.offsetTop);
                   this.themTopY.push(this.$refs.recommend.$el.offsetTop);
			},
           detailid(){
              request({
                url:"/detail",
                params:{
                    iid:this.iid
                }
              }).then(res =>{
                const data = res.data.result;
                //
                this.topimages = data.itemInfo.topImages;
                //
                this.goods = new Goods(data.itemInfo,data.columns,data.shopInfo.services);
                //
                this.shop = new Shop(data.shopInfo);
                this.detailInfo = data.detailInfo;

                this.paramInfo = new GoodsParam(data.itemParams.info,data.itemParams.rule)
                //
                if(data.rate.cRate !==0){
                   this.commentInfo = data.rate.list[0];
                }
              }).catch(err =>{
                 console.log(err)
              }) 
           },
          },
		components:{
			detailnav,
			detailshuf,
			detailbase,
			detailshop,
			scroll,
			detailinfo,
			paraminfo,
			detailcomment,
			detaillist,
			detailbar,
			top
		}
	}
</script>
<style scoped>
#detail{
	position: relative;
	z-index: 9;
	background-color: #fff;
	height: 100vh;
}
.showCart{
	position: fixed;
	top: 50%;
	left: 50%;
	transform: translate(-50%,-50%);
	z-index: 500000;
	text-align: center;
	border-radius: 7px;
	width: 170px;
	height: 35px;
	line-height: 35px;
	font-size: 20px;
	font-weight: 700;
	color: #fff;
    background-color: rgba(0,0,0,0.5);
}
.nav{
	position: relative;
	z-index: 100;
	background-color: #fff;
}
.content{
	height: calc(100% - 93px);
	overflow: hidden;
}
</style>