<template>
	<div class="goodlist">
		<div v-for="(item,index) in good" :key="index" class="goods-item" @click="detail(index)">
			<img :src="item.image" alt="" @load="imageLoad" />
            <div class="goods-info">
                <p>{{item.title}}</p>
                <span class="price">{{item.price}}</span>
                <span class="collect">{{item.cfav}}</span>
            </div>
		</div>
	</div>
</template>
<script>
	export default{
		name:"detaillist",
		props:{
			good:{
				type:Array,
				default(){
					return []
				}
			}
		},
		methods:{
			imageLoad(){
					this.$emit('imageLoad')
			},
            detail(index){
               const ii = this.good[index].item_id;
               this.$emit('rout',ii)
             //  this.$router.push('/detail/'+this.good[index].item_id);
            }
		}
	}
</script>
<style scoped>
    .goodlist{
       padding-top: 35px; 
       display: flex;
       flex-wrap: wrap;
    }
	.goods-item{
		padding-bottom: 40px;
		position: relative;
		width: 50%;
	}
	.goods-item img{
		padding: 3px 3px;
		width: 100%;
		height: 100%;
		border-radius: 5px;
	}
	.goods-info{
		font-size: 14px;
		position: absolute;
		bottom: 5px;
		left: 0;
		right: 0;
		overflow: hidden;
		text-align: center;
	}
	.goods-info p{
		overflow: hidden;
		padding: 0 3px;
		text-overflow: ellipsis;
		white-space: nowrap;
		margin-bottom: 3px;
	}
	.goods-info .price{
		color: #ff5777;
		margin-right: 20px;
	}
	.goods-info .collect{
		position: relative;
	}
	.goods-info .collect::before{
		content: '';
		position: absolute;
		left: -15px;
		top: -1px;
		width: 16px;
		height: 16px;
		background: url('~@/assets/img/home/favor.png') 0 0/16px 16px;
	}
</style>