<template>
	<div v-if="Object.keys(detailInfo).length !== 0" class="goods-info">
		<div class="info-desc">
			<div class="desc">{{detailInfo.desc}}</div>
		</div>
		<div class="info-key">{{detailInfo.detailImage[0].key}}</div>
		<div class="info-list">
			<img v-for="(item, index) in detailInfo.detailImage[0].list" :key="index" :src="'http://'+item" @load="imgLoad" alt=""/>
		</div>
	</div>
</template>
<script>
	export default{
		name:"detailinfo",
		props:{
			detailInfo:{
				type:Object,
				default(){
					return {}
				}
			}
		},
		data(){
			return{
				counter:0,
				imagesLength:0
			}
		},
		methods:{
			imgLoad(){
				if(++this.counter === this.imagesLength){
					this.$emit('imageLoad')
				}
			}
		},
		watch:{
          detailInfo(){
             this.imagesLength = this.detailInfo.detailImage[0].list.length;
          }
		}
	}
</script>
<style scoped>
	.goods-info{
		padding: 20px 0;
		border-bottom: 5px solid #f2f5f8;
	}
	.info-list img{
		width: 100%;
	}
	.info-key{
		width: 100%;
		padding: 5px;
		text-align: center;
		background-color: #dcdcdc;
		font-size: 16px;
		font-weight: 500;
		margin-bottom: 20px;
	}
	.info-desc{
		padding: 10px 5px 20px 5px;
	}
</style>