<template>
	<div class="detailnav">
		<navbar class="navbar">
			<div slot="left" class="back" @click="back">
				<img src="@/assets/img/detail/back.png" alt="">
			</div>
			<div slot="content" class="title">
				<div v-for="(item,index) in titles" :key="index" class="title-item" :class="{active:index === currentIndex}" @click="titleClick(index)">
					{{item}}
				</div>
			</div>
		</navbar>
	</div>
</template>
<script>
	import navbar from "@/components/navbar/navbar"
	export default{
		name:"detailnav",
		data(){
           return{
            titles:['','','',''],
            currentIndex:0
           }
		},
		methods:{
			back(){
				this.$router.back();
			},
          titleClick(index){
            this.currentIndex = index;
            this.$emit('title', index);
          }
		},
		components:{
			navbar
		}
	}
</script>
<style scoped>
	.title{
		display: flex;
		font-size: 14px;
	}
	.title-item{
		flex: 1;
	}
	.active{
		color: #ff5777;
	}
	.navbar{
		border-bottom: 1px solid #dcdcdc;
	}
	.back img{
		position: relative;
		left: -10px;
		vertical-align: middle;
		width:30px;
		height: 30px;
	}
</style>